.feedback-notices .feedback-notice {
  padding: 10px 16px;
  border-radius: 4px;
  box-shadow: 0 8px 8px -4px rgba(69, 121, 245, 0.06), 0 2px 4px -1px rgba(20, 16, 41, 0.06), 0 0 1px 0 rgba(69, 121, 245, 0.12);
  background-color: #fafbff;
  width: 250px;
  font-size: 16px;
  line-height: 28px;
  cursor: pointer; }
  .feedback-notices .feedback-notice.fixed {
    position: fixed;
    right: 40px;
    bottom: 40px; }
  .feedback-notices .feedback-notice.info {
    color: #4579f5;
    background: rgba(69, 121, 245, 0.03); }
  .feedback-notices .feedback-notice.success {
    color: #16b862;
    background: rgba(22, 184, 98, 0.03); }
  .feedback-notices .feedback-notice.warning {
    color: #ffaa00;
    background: rgba(255, 170, 0, 0.03); }
  .feedback-notices .feedback-notice.error {
    color: #f54562;
    background: rgba(245, 69, 98, 0.03); }
